/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&subset=cyrillic,cyrillic-ext");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: '';
  content: none;
}

q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span,
a:focus {
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

body {
  font-family: "Roboto Regular", sans-serif;
  font-size: 1.6rem;
  background-color: orange;
  position: relative;
  color: #000;
  line-height: 1.4;
  place-items: '';
}

.grid {
  grid-gap: 2rem;
  display: grid;
}

@supports (grid-area: auto) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
  }
  .grid > :nth-of-type(1) {
    grid-column-end: span 7;
    grid-row-end: span 1;
  }
  .grid > :nth-of-type(2) {
    grid-column-end: span 5;
    grid-row-end: span 1;
  }
}

.container {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  margin-right: auto;
  margin-left: auto;
}

@media only screen and (min-width: 36em) {
  .container {
    max-width: 55.4rem;
  }
}

@media only screen and (min-width: 48em) {
  .container {
    max-width: 75rem;
  }
}

@media only screen and (min-width: 62em) {
  .container {
    max-width: 97rem;
  }
}

@media only screen and (min-width: 75em) {
  .container {
    max-width: 117rem;
  }
}

@media only screen and (min-width: 105em) {
  .container {
    width: 1680px;
    max-width: 90%;
  }
}
